// see https://projects.lukehaas.me/css-loaders/

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  animation-fill-mode: both;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
  color: #ffffff;
  font-size: 10px;
  margin: 80px auto;
  position: relative;
  text-indent: -9999rem;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader:before {
  left: -2.5rem;
  animation-delay: -0.32s;
}
.loader:after {
  left: 2.5rem;
}

@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 1.5rem 0 -1.3rem;
  }
  40% {
    box-shadow: 0 1.5rem 0 0;
  }
}



