.sizer {
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
}

.poster,
.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}



